<template>
  <div class="info-collector">
    <h2></h2>
    <van-form @submit="submitInfo">
      <van-cell-group inset>
        <van-field
            v-model="name"
            type="name"
            name="name"
            label=""
            placeholder=""
            :rules="[{ required: true, message: '' }]"
        />
        <van-field
            v-model="location"
            is-link
            readonly
            name="picker"
            label=""
            placeholder=""
            @click="showLocPicker = true"
        />
        <van-popup v-model:show="showLocPicker" position="bottom" round>
          <van-picker
              :columns="locationList"
              @confirm="confirmLoc"
              @cancel="showLocPicker = false"
          >
          </van-picker>

        </van-popup>
      </van-cell-group>
      <van-row justify="space-between" style="margin: 16px" @click="quitApp">
        <van-col :span="10">
          <van-button block round type="default" size="normal">
            
          </van-button>
        </van-col>
        <van-col :span="10">
          <van-button block round type="primary" native-type="submit" size="normal">
            
          </van-button>
        </van-col>
      </van-row>
    </van-form>
    <van-dialog title="" :show="showLocDialog" show-cancel-button @confirm="addLocConfirm" @cancel="showLocDialog = false">
      <van-field
          v-model="location"
          type="location"
          name="location"
          label=""
          placeholder=""
          :rules="[{ required: true, message: '' }]"
      />
    </van-dialog>
  </div>
</template>

<script>
import { Form, Field, CellGroup, Button, Picker, Popup, Dialog, Notify, Row, Col } from 'vant';

export default {
  name: 'InfoCollector',
  components: {
    'van-form': Form,
    'van-field': Field,
    'van-cell-group': CellGroup,
    'van-button': Button,
    'van-picker': Picker,
    'van-popup': Popup,
    // 'van-dialog': Dialog.component,
    [Dialog.Component.name]: Dialog.Component,
    'van-row': Row,
    'van-col': Col,
  },
  data() {
    return {
      visible: true,
      location: '',
      name: '',
      locationList: ['', '', '', '', '', '', '', '', '', ''],
      showLocPicker: false,
      showLocDialog: false
    }
  },
  created() {
    let list = window.localStorage.getItem('test-demo-location-list')
    let name = window.localStorage.getItem('test-demo-name')
    if (list !== undefined && list !== null) {
      this.locationList = JSON.parse(list)
    }
    if (name !== undefined && name !== null) {
      this.name = name
    }
  },
  methods: {
    submitInfo() {
      if (!this.name || !this.location) {
        return
      }
      window.localStorage.setItem('test-demo-name', this.name)
      window.localStorage.setItem('test-demo-location-list', JSON.stringify(this.locationList))


      this.$router.push({
        path: '/result',
        query: {
          name: this.name,
          location: this.location
        }
      })
    },
    confirmLoc(value) {
      if (value === '') {
        this.showLocDialog = true
        this.showLocPicker = false
        this.location = ''
        return false
      }
      this.location = value
      this.showLocPicker = false
    },
    addLocConfirm() {
      this.location = this.location.trim()
      if (this.location === '' || this.location.length === 0) {
        Notify({ type: 'warning', message: '' });
        return
      }
      if (this.locationList.includes(this.location)) {
        Notify({ type: 'warning', message: '' });
        return
      }

      this.locationList.push(this.location)
      this.showLocDialog = false
    },
    quitApp() {
      console.log(this)
    }
  },
}
</script>

